:root {
  --background: #000000;
  --secondaryColor: #212529;
  --thirdColor: #C4976E;
  --redColor: #501b1d;
}

#root {
  font-size: 28px;
  background-color: var(--background);
  font-family: 'Helvetica';
}

.menuCard {
  font-size: 16px;
  color: white;
  border-bottom: var(--thirdColor) solid;
  padding: 4px;
  z-index: 0 !important;
}

.menuCard > * {
  z-index: 0 !important;
}

.menuTitle {
  margin-top: 40px;
  color: white;
  border-bottom: var(--thirdColor) solid;
}

.dropdown-menu {
  opacity: 1 !important;
}

.menuDrop {
  opacity: 1 !important;
  z-index: 1111 !important;

}

.menuDrop > * {
  background-color: var(--background) !important;
  color: var(--thirdColor) !important;
  font-weight: bolder !important;
  opacity: 1 !important;
  padding: 0 !important;
  border-color: white !important;
}

.dropdown-item {
  opacity: 1;
  font-weight: bold !important;
  border-bottom: solid var(--secondaryColor) !important;
  color: var(--thirdColor) !important;
}

#dropdown-basic-button {
  padding: 8px 16px !important;
}

.navbar {
  opacity: 1 !important;
  background-color: var(--background);
}

.open {
  /* padding-right: 40px; */
  font-size: 16px;
}

.current {
  font-weight: bolder;
  font-size: 16px;
}
.order {
  cursor: pointer;
}

.logo {
  margin-left: 20px;
}

.dropCont {
  background-color: white !important;
}

h2 {
  color: var(--thirdColor) !important;
  font-size: 36px !important;
  font-weight: bolder !important;
  -webkit-text-stroke-width: .5px;
  -webkit-text-stroke-color: white;
}

.orderDrop {
  font-size: 24px;
  margin-bottom: 5px;
}